:root {
  --background-color: #22333B;
  --text-color: #E0E0E0;
  --primary-color: #1BB8CC;
  --secondary-color: #0A3D91;
  --error-color: #B22222;
}

[data-theme='dark'] {
  --background-color: #22333B;
  --text-color: #E0E0E0;
  --primary-color: #1BB8CC;
  --secondary-color: #0A3D91;
  --error-color: #B22222;
}

[data-theme='light'] {
  --background-color: #FFFFFF;
  --text-color: #000000;
  --primary-color: #4CAF50;
  --secondary-color: #FFC107;
  --error-color: #F44336;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  text-align: center;
  background-color: var(--background-color);
  padding: 5px;
}

.input-container {
  margin: 3px;
  padding: 3px;
  border: 1px solid var(--background-color);
  border-radius: 5px;
  background-color: var(--background-color);
  width: 32%;
  text-align: center;
  box-sizing: border-box;
  transition: transform 0.2s;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.input-container:hover {
  transform: scale(1.05);
}

.input-box {
  padding: 3px;
  margin-bottom: 3px;
  font-size: 12px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 90%;
  text-align: center;
  background-color: white;
  color: var(--text-color);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.input-box::placeholder {
  color: var(--text-color);
  opacity: 0.7;
}

.top-input {
  background-color: var(--secondary-color);
}

.bottom-input {
  background-color: var(--error-color);
}

.team-data-container {
  text-align: center;
  background-color: var(--background-color);
  border-radius: 5px;
  padding: 3px;
  margin-top: 0;
  border: 1px solid #4d5255;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section-header {
  font-weight: bold;
  margin-top: 3px;
  margin-bottom: 3px;
  font-size: 17px;
  color: var(--text-color);
}

.grid-container {
  display: flex;
  flex-direction: column;
  gap: 1px;
}

.grid-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  padding: 2px 5px;
  background-color: var(--primary-color);
  font-size: 15px;
  color: var(--background-color);
  border-radius: 3px;
  width: 250px;
}

#heatmapContainer {
  margin-top: 20px;
  position: relative;
  width: 1000px;
  height: 500px;
  transition: transform 0.2s;
}

.dropdown-container {
  position: fixed;
  bottom: 10px;
  left: 10%;
  transform: translateX(-50%);
  background-color: var(--background-color);
  border: 1px solid var(--background-color);
  border-radius: 5px;
  padding: 10px;
  z-index: 1000;
  transition: transform 0.2s;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.dropdown-container select {
  padding: 5px;
  font-size: 16px;
  background-color: var(--background-color);
  color: var(--text-color);
  border-radius: 5px;
}

.dropdown-container:hover {
  transform: translateX(-50%) scale(1.05);
}

.dropdown-container-teams {
  position: fixed;
  bottom: 10px;
  right: 1%;
  transform: translateX(-50%);
  background-color: var(--background-color);
  border: 1px solid var(--background-color);
  border-radius: 5px;
  padding: 10px;
  z-index: 1000;
  transition: transform 0.2s;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.dropdown-container-teams select {
  padding: 5px;
  font-size: 16px;
  background-color: var(--background-color);
  color: var(--text-color);
  border-radius: 5px;
}

.spinner-container {
  position: fixed;
  bottom: 10px; 
  left: 10px; 
  z-index: 1000;
}

.spinner {
  border: 8px solid #f3f3f3; 
  border-top: 8px solid #3498db; 
  border-radius: 50%;
  width: 60px; 
  height: 60px; 
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.single-team-container, .team-comparison-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.multiple-teams-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.chart-container {
  width: 80%;
  margin-top: 20px;
  background-color: var(--background-color);
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  transition: transform 0.2s;
  z-index: 11;
}

.input-boxes-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  gap: 10px;
}

.field-selection-container {
  display: flex;
  justify-content: center;
  margin: 20px 0; 
  z-index: 10; 
  position: relative; 
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.field-selection-container select {
  padding: 5px;
  font-size: 16px;
  background-color: var(--background-color);
  color: var(--text-color);
  border-radius: 5px;
  border: 1px solid var(--text-color);
  z-index: 11; 
}

.field-selection-container:hover {
  transform: scale(1.05); 
}

.pie-charts-container {
  color: var(--text-color);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  padding: 20px;
  justify-items: center;
  z-index: 11
}

.formula-section {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: var(--background-color);
  padding: 5px;
  border: 1px solid #4d5255;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.formula-input {
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.formula-input label {
  color: var(--text-color);
  margin-bottom: 5px;
}

.formula-input select,
.formula-input input {
  padding: 5px;
  font-size: 16px;
  background-color: var(--background-color);
  color: var(--text-color);
  border-radius: 5px;
  border: 1px solid var(--text-color);
  margin-bottom: 10px;
}

.formula-input select:hover,
.formula-input input:hover {
  transform: scale(1.05);
}

.calculate-button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: var(--primary-color);
  color: var(--background-color);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.2s;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.calculate-button:hover {
  transform: scale(1.05);
}

.match-messages-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  background-color: var(--background-color);
  color: var(--text-color);
  padding: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  margin-top: 20px;
}

.match-message {
  margin: 10px 0;
  padding: 10px;
  border: 1px solid #4d5255;
  border-radius: 5px;
  background-color: #1E2C2F;
  width: 80%;
  text-align: left;
}

.match-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #4d5255;
  padding-bottom: 5px;
  margin-bottom: 5px;
}

.match-header h3 {
  margin: 0;
  color: var(--primary-color);
}

.match-header p {
  margin: 0;
  color: #FF6347;
}

.match-stats, .match-analysis {
  margin: 10px 0;
}

.match-stats p, .match-map p {
  margin: 3px 0;
}

.match-stats span {
  font-weight: bold;
  color: var(--primary-color);
}

.analysis-section {
  margin: 10px 0;
}

.analysis-section h4 {
  margin: 0 0 5px;
  font-size: 19px;
  color: var(--primary-color);
}

.analysis-section p {
  margin: 3px 0;
  display: flex;
  align-items: center;
}

.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.pagination button {
  margin: 0 5px;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: var(--primary-color);
  color: var(--background-color);
  cursor: pointer;
  transition: transform 0.2s;
}

.pagination button:hover {
  transform: scale(1.05);
}

.pagination button.active {
  background-color: #FF6347;
  color: white;
}

.menu-icon {
  position: fixed;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  z-index: 1000;
}

.menu-line {
  width: 100%;
  height: 4px;
  background-color: var(--text-color);
  transition: transform 0.2s;
}

.menu {
  position: fixed;
  top: 50px;
  right: 10px;
  background-color: var(--background-color);
  border: 1px solid var(--text-color);
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.menu button {
  padding: 10px;
  font-size: 16px;
  background-color: var(--background-color);
  color: var(--text-color);
  border: 1px solid var(--text-color);
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.2s;
}

.menu button:hover {
  transform: scale(1.05);
}

.pit-scouting-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 20px;
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: 'Roboto', Arial, sans-serif;
}

.search-bar {
  width: 90%;
  margin-bottom: 20px;
}

.data-table {
  margin-top: 20px;
  width: 90%;
}

.MuiPaper-root {
  background-color: var(--background-color);
  color: var(--text-color);
}

.MuiTableCell-root {
  color: var(--text-color);
  border-color: #4d5255;
  padding: 8px 16px;
}

.MuiTableCell-head {
  background-color: var(--primary-color);
  color: var(--background-color);
  font-weight: bold;
}

.MuiTableRow-root:nth-of-type(even) {
  background-color: #2E3B3F;
}

.add-column-button, .calculate-button {
  background-color: var(--background-color);
  color: var(--text-color);
  border: none;
  padding: 0.5rem 1rem;
  margin-top: 0.5rem;
  cursor: pointer;
  border-radius: 4px;
}

.add-column-button:hover, .calculate-button:hover {
  background-color: #1b2a34;
}

.add-column-button {
  margin-right: 0.5rem;
}

.remove-column-button {
  margin-left: 0.5rem;
  background-color: #ff4d4d;
  color: white;
  border: none;
  padding: 0.5rem;
  cursor: pointer;
  border-radius: 4px;
}

.remove-column-button:hover {
  background-color: #cc0000;
}
